.page-header {


}
.page-header-wrapper {
  position: relative;
  width: 1920px;
  height: 900px;
  margin: 0 auto;
}
.page-header-inner {
  position: absolute;
  z-index: 1000;
  left: 405px;
  display: flex;
  justify-content: space-between;
  align-self:center;
  margin: 0 auto;
  width: 1110px;
  height: 100px;

}
.page-header-img {
  width: 100px;
  height: 100px;
  
}
.page-header-logo {
  font-family: "Satoshi-Regular", sans-serif;
  margin-top: 40px;
  font-size: 70px;
}
.nav {
  position: absolute;
  z-index: 1001;
  left: 405px;
  top: 100px;
  display: flex;
  margin: 0 auto;
  width: 1110px;
  height: 50px;
}
.page-header-item a{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 285px;
  height: 48px;
  border: 1px solid @basic_white;
  font-family: "Satoshi-Regular", sans-serif;
  font-size: 14px;
  line-height: 150%;
  transition: 0.5s;
  &:after {
    display: block;
    position: absolute;
    top: 32px;
    left: 85px; /*изменить на right:0;, чтобы изменить направление подчёркивания */
    width: 0;/*задаём длинну линии до наведения курсора*/
    height: 2px; /*задаём ширину линии*/
    background-color:@basic_white; /*задаём цвет линии*/
    content: "";
    transition: width 0.3s ease-out; /*задаём время анимации*/
  }

  &:hover:after,
  &:focus:after {
    left: 85px;
	width: 113px; /*устанавливаем значение 100% чтобы ссылка подчёркивалась полностью*/
  }
  &:hover {
    font-size: 18px;
    color:@basic_white;
  }
}

.page-header-item a {
  
}

.page-header-item-left a{
  border-left: none;
}
.page-header-item-right a{
  border-right: none;
}

