.preview-background-1 {
  position: absolute;
  width: 1920px;
  height: 900px;
  background-image: url("../img/preview-background-1.png");
  background-size: contain;
}
.preview-background-2 {
  position: absolute;
  width: 1920px;
  height: 900px;
  background-image: url("../img/preview-background-2.jpg");
  clip-path: circle(0 at 0 0);
  background-size: 1920px 900px;
  background-repeat: no-repeat;
}
.preview-buttons {
  display: flex;
  justify-content: space-between;
  position: absolute;
  left: 752px;
  top: 700px;
  width: 415px;
  height: 50px;
}
.preview-buttons-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Satoshi-Medium", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  width: 182px;
  height: 50px;
  background-color: @basic_grey;
  box-shadow: 0 0 5px 5px rgb(170, 154, 103);
  
  &:hover {
    animation-name: header-arrow-desc;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  
    @keyframes header-arrow-desc{
      0%{
        font-size: 16px;
      }
  
      50% {
        font-size: 17px;
        opacity: 0.9;
      }
    
      100%{
        font-size: 16px;
      }
    }
  }


}
