.collection {
  width: 100%;
  height: auto;
  padding-bottom: 100px;
  background-color: @basic-beige;
  color: @special_dark;
}
.wine-map {
  width: 1110px;
  height: auto;
  margin: 0 auto;
}
.wine-map-title {
  width: 240px;
  font-family: "Satoshi-Regular", sans-serif;
  font-size: 36px;
  line-height: 150%;
  margin: 0 auto;
  padding-top: 70px;
}
.wine-map-inner {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
  width: 1110px;
  height: 350px;
}

.wine-map-subtitle {
  width: auto;
  text-align: center;
  font-family: "Satoshi-Bold", sans-serif;
  font-size: 26px;
  line-height: 150%;
}
.wine-map-type,
.wine-map-content {
  width: 540px;
  height: 300px;
  background-color: #e9deb5;
  box-shadow: 20px;
  border-radius: 20px;
}

.wine-map-type-inner,
.wine-map-content-inner {
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
  width: 100%;
}
.wine-map-type-subtitle,
.wine-map-content-subtitle {
  display: block;
  font-family: "Satoshi-Medium", sans-serif;
  font-size: 22px;
  line-height: 150%;
  color: @special_dark;
  border-radius: 10px;
  box-shadow: 0 0 10px 1px #aa9a67;
}
.wine-map-type-text,
.wine-map-content-text {
  display: none;
  font-family: "Satoshi-Medium", sans-serif;
  font-size: 18px;
  line-height: 130%;
  color: @special_dark
}
.wine-map-type-hover1,
.wine-map-type-hover2,
.wine-map-type-hover3,
.wine-map-type-hover4 {
  position: relative;
}
.wine-map-type-hover1:hover .wine-map-type-text {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: -150px;
  right: -431px;
  width: 480px;
  height: 120px;
  text-align: center;
  border: 2px solid #aa9a67;
}
.wine-map-type-hover2:hover .wine-map-type-text {
  display: flex;
  align-items: center;
