.wine-map-content {

}
.wine-map-content-subtitle {
}
.wine-map-content-text {
  display: none;
}

.novelty-title {
  text-align: center;
  text-transform: uppercase;
  margin: 30px auto;
  font-family: "Satoshi-Regular", sans-serif;
  font-size: 36px;
  line-height: 150%;
}
.novelty-wrapper {
  display: flex;
  overflow: hidden;
  width: 900px;
  height: 400px;
  margin: 0 auto;
  background-color: #fff;
}
.novelty-link {
  margin-right: 20px;
  width: 900px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.novelty-img-wrapper {
  width: 450px;
}
.novelty-img {
  margin-top: 20px;
  width: 370px;
  height: auto;
  object-fit: cover;
}

.novelty-inner {
  width: 450px;
  height: 400px;
}
.novelty-subtitle {
  width: 390px;
  margin: 30px auto;
  text-align: center;
  font-family: "Satoshi-Bold", sans-serif;
  font-size: 26px;
  line-height: 140%;

  color: @special_dark
}
.novelty-price {
  display: block;
  text-align: center;
  margin: 0 auto;
  font-family: "Satoshi-Regular", sans-serif;
  font-size: 20px;
}
.novelty-table {
  margin: 0 auto;
  margin-top: 30px;
  width: 380px;
  height: 150px;
  text-align: center;
  text-transform:capitalize;
}
th {
  width: 50%;
  border-right: 1px solid @basic_dark;
}
td {
  width: 50%;
}
tr {
  border-bottom: 1px solid @basic_dark;
}